























import {
  defineComponent, watch, ref,
} from '@vue/composition-api';
import 'intersection-observer';
import Intersect from 'vue-intersect';

enum SearchbarMode {
  FIX = 'FIX',
  SLOT = 'SLOT'
}

export default defineComponent({
  components: {
    Intersect,
  },
  render() {
    if (!this.$scopedSlots.default) throw new Error('default插槽不能为空');
    if (!this.$scopedSlots.searchbar) throw new Error('searchbar插槽不能为空');
    const d = this.$scopedSlots.default(null);
    if (!d) throw new Error('default插槽需要返回VNode');
    if (this.searchbarMode === SearchbarMode.SLOT) {
      if (!this.$scopedSlots.searchbar) throw new Error('searchbar插槽不能为空');
      if (d[0].componentOptions) {
        this.$set(d[0].componentOptions, 'children', this.$scopedSlots.searchbar(null)!);
      }
      return <section key='sec'>
        <Intersect threshold={[0.5]} onchange={([{ intersectionRatio }]: IntersectionObserverEntry[]) => {
          if (intersectionRatio <= 0.5) this.searchbarMode = SearchbarMode.FIX;
        }}>
          {d}
        </Intersect>
      </section>;
    }
    if (d[0]!.componentOptions!.children?.length) {
      this.$delete(d[0].componentOptions!, 'children');
    }
    return <section key='sec'>
      <transition name='topbar' appear>
        <div class="fixed-to-top flex-center">{this.$scopedSlots.searchbar(null)}</div>
      </transition>
      <Intersect threshold={[0.5]} onchange={([{ intersectionRatio }]: IntersectionObserverEntry[]) => {
        if (intersectionRatio >= 0.5) this.searchbarMode = SearchbarMode.SLOT;
      }}>
        {d}
      </Intersect>
    </section>;
  },
  // @ts-ignore
  setup() {
    return {
      searchbarMode: ref(SearchbarMode.SLOT),
    };
  },
});
