




















import { defineComponent, ref, watch } from '@vue/composition-api';


export default defineComponent({
  props: {
    value: {
      type: String,
    },
    clearInputTriggerSearch: {
      type: Boolean,
      default: true,
    },
    placeholder: String,
  },
  setup(props, { emit }) {
    const innerValue = ref(props.value);
    watch(() => props.value, (v) => {
      innerValue.value = v;
    });
    return {
      innerValue,
      clear() {
        innerValue.value = '';
        emit('input', '');
        emit('clear');
        // if (props.clearInputTriggerSearch) {
        //   emit('search', '');
        // }
      },
    };
  },
});

