import axios from './axios';

export type ICategoryTree = {
  name: string
  id: string
  sub?: ICategoryTree[]
};

let category:ICategoryTree[];

async function recursiveGetCategory(level: number, currentLevel: number, parent:ICategoryTree) {
  if (currentLevel <= level) {
    if (!parent.sub) {
      parent.sub = (await axios.get<ICategoryTree[]>(`/show/category/list?parentId=${parent.id}`)).data;
    }
    await Promise.all(parent.sub.map(async p => recursiveGetCategory(level, currentLevel + 1, p)));
  }
}

export default async function getCategory(level: number = 3) {
  if (!category) {
    category = (await axios.get<ICategoryTree[]>('/show/category/list?parentId=-1')).data;
  }
  await Promise.all(category.map(async (p) => recursiveGetCategory(level, 2, p)));
  // 防止外部污染
  return JSON.parse(JSON.stringify(category)) as ICategoryTree[];
}

export async function getMateiralCategory() {
  await getCategory();
  return JSON.parse(JSON.stringify(category[0].sub)) as ICategoryTree[];
}
