
import {
  computed, defineComponent, ref, watch,
} from '@vue/composition-api';
import useLoader from '@4dst-saas/public-utils/dist/composables/use-loader';
import axios from '@/api/axios';
import { MaterialGoods } from '@/models/MaterialGood';
import Paging from '@/models/Paging';
import { VNode } from 'vue';
import Intersect from 'vue-intersect';
import { getMateiralCategory, ICategoryTree } from '@/utils/get-material-category';
import i18n from 'i18next';
import ImagePicker from '@/views/file-list/components/catalog/ImagePicker.vue';
import open from '@/utils/open-window';
import DisplayModeSwitcher from '../components/DisplayModeSwitcher.vue';
import MaterialList from '../components/CommodityList.vue';
import TopContent from './components/TopContent';
import TopContentWrapper from './components/TopContentWrapper.vue';
import SearchBar from './components/SearchBar.vue';
import Rows from './components/Rows.vue';
import ClassificationListHeader from './components/ClassificationListHeader.vue';
// import RecommandMerchant from './components/RecommandMerchant';


export default defineComponent({
  render() {
    const generateMaterialList = (list: MaterialGoods[], rowsLimit = true) => {
      if (!list.length) {
        return <span style={{
          marginTop: '20px',
          color: 'gray',
        }}>暂无面料</span>;
      }
      const mList = <MaterialList style={{ maxHeight: '572px', overflowY: 'hidden', margin: '0 -15px' }} materialList={list}
        displayMode={this.displayMode}></MaterialList>;
      if (!rowsLimit) return mList;
      return mList;
    };
    let lists: VNode | VNode[];
    const loadingPlaceholder = <div vLoading={true} style={{ height: '200px' }}></div>;
    if (!this.lists) {
      lists = loadingPlaceholder;
    } else if (this.currentCategory === 0) {
      // 当前一级类目为全部时，显示所有一级类目的推荐列表
      lists = this.lists.map((list, i) => <ClassificationListHeader title={
        this.$t('material-mall.recommend_placeholder', [this.$t(`material.material-category.${list.name}`)])
      } showMore
        jumpTo={i + 1} onchange={(c: number) => { this.currentCategory = c; }}>
        {generateMaterialList(list.list)}
      </ClassificationListHeader>);
    } else if (this.list) {
      // 否则显示当前一级类目的推荐列表
      lists = [
        // <RecommandMerchant key='recommandMerchant' categoryId={this.currentCategory}></RecommandMerchant>,
        <ClassificationListHeader title={this.$t('material-mall.recommend_for_you')}>
          {generateMaterialList(this.list.list, false)}
        </ClassificationListHeader>,
      ];
      if (this.canLoadMore) {
        lists.push(<Intersect onenter={() => this.loadMore(this.list)}>
          <div class="load-more">
            {
              this.loadingPage && this.loadingPage > 1
                ? [
                  <i class="el-icon-loading" />,
                  <span>{i18n.t('material-mall.loading')}</span>,
                ]
                : <span>{i18n.t('material-mall.scroll_to_load')}</span>
            }
          </div>
        </Intersect>);
      }
    } else {
      lists = loadingPlaceholder;
    }
    return <section style={{ minWidth: '1330px', padding: '20px', position: 'relative' }} class='flex-column'>
      <TopContentWrapper scopedSlots={{
        default: () => <TopContent categoryList={this.materialCategoryTree || []} vModel={this.currentCategory}></TopContent>,
        searchbar: () => <SearchBar onsearch={(condition: string) => {
          if (this.isQt()) {
            this.$root.$router.push({
              path: '/qt/material-mall/list',
              query: {
                // ...this.$root.$route.query,
                searchParamsStr: JSON.stringify({
                  condition,
                }),
              },
            });
          } else {
            open({
              path: '/material-mall/list',
              query: {
                // ...this.$root.$route.query,
                searchParamsStr: JSON.stringify({
                  condition,
                }),
              },
            });
          }
        }}>
          <el-popover slot="append-icon">
            <ImagePicker onselect-image={(file: File) => {
              if (this.isQt()) {
                this.$root.$router.push({
                  path: '/qt/material-mall/list',
                  query: {
                    // ...this.$root.$route.query,
                    // @ts-ignore
                    file: URL.createObjectURL(file),
                    searchParamsStr: JSON.stringify({}),
                  },
                });
              } else {
                open({
                  path: '/material-mall/list',
                  query: {
                    // ...this.$root.$route.query,
                    // @ts-ignore
                    file: URL.createObjectURL(file),
                    searchParamsStr: JSON.stringify({}),
                  },
                });
              }
            }} />
            <i slot="reference" class="flex-vertical-center i-camera">
              <span>{this.$t('material-mall.search_by_photo')}</span>
            </i>
          </el-popover>
        </SearchBar>,
      }}></TopContentWrapper>

      <div class="flex-justify-flex-end" style={{ position: 'relative' }}>
        <DisplayModeSwitcher vModel={this.displayMode} style={{
          position: 'absolute',
          top: '32px',
        }}></DisplayModeSwitcher>
      </div>

      {
        lists
      }
    </section>;
  },
  inject: ['isQt'],
  setup(_, { root: { $route, $router, $set } }) {
    const currentCategory = ref(+$route.query?.c || 0);
    watch(() => currentCategory.value, (nc) => {
      $router.replace({
        query: {
          ...$route.query,
          // @ts-ignore
          c: nc,
        },
      });
    });
    const searchByImage = ref(false);
    const materialCategoryTree = useLoader(getMateiralCategory);
    const lists = useLoader(async (_c) => {
      // @ts-ignore
      const _cTree = _c.value as ICategoryTree[];
      if (!_cTree) return null;
      const idsList = _cTree.map(cs => cs.sub?.map(c => c.id));
      const cList = (await Promise.all(idsList.map(async ids => {
        return (await axios.post<Paging<MaterialGoods>>('/mall/mall/search', {
          category: ids,
          size: 15,
        })).data;
      })));
      const resList: {
        name: string,
        list: MaterialGoods[],
        ids: string[],
        pages: number,
        current: number,
      }[] = [];
      for (let i = 0; i < _cTree.length; i++) {
        const { pages, current, records: list } = cList[i];
        resList.push({
          name: _cTree[i].name,
          ids: idsList[i]!,
          pages,
          current,
          list,
        });
      }
      return resList;
    }, materialCategoryTree, {
      initialValue: null,
      loadingValue: () => null,
    });

    const list = useLoader(({ lists: ls, currentCategory: cc }) => {
      if (!ls || cc === 0) return null;
      return ls[cc - 1];
    }, {
      lists,
      currentCategory,
    }, {
      initialValue: null,
    });

    const canLoadMore = computed(() => {
      if (!list.value) return false;
      return list.value.current < list.value.pages;
    });
    const loadingPage = ref<number | null>(null);

    async function loadMore(l: typeof list.value) {
      if (l && l.pages > l.current) {
        loadingPage.value = l.current + 1;
        const res = (await axios.post<Paging<MaterialGoods>>('/mall/mall/search', {
          category: l.ids,
          size: 15,
          page: ++l.current,
        })).data;
        l.list.push(...(res.records || []));
        loadingPage.value = null;
      }
    }
    return {
      currentCategory,
      searchByImage,
      lists,
      list,
      materialCategoryTree,
      displayMode: ref<'drape' | 'flat'>('drape'),

      loadMore,
      canLoadMore,
      loadingPage,
    };
  },
});
