




























import selectFiles from '@/utils/select-files';
import validFile from '@/utils/validators/valid-file';
import {
  defineComponent, ref, toRef, watch,
} from '@vue/composition-api';

export default defineComponent({
  props: {
    image: {
      type: undefined as unknown as () => Blob,
    },
  },
  model: {
    prop: 'image',
    event: 'select-image',
  },
  setup(props, { emit }) {
    const enterEvent = ref<DragEvent>();
    const url = ref<string>();
    watch(toRef(props, 'image'), (image) => {
      if (url.value) {
        URL.revokeObjectURL(url.value);
      }
      if (image) {
        url.value = URL.createObjectURL(image);
      } else {
        url.value = undefined;
      }
    }, {
      flush: 'pre',
      immediate: true,
    });
    return {
      async onClick() {
        const [file] = await selectFiles(['png', 'jpg'], 5000);
        await validFile(file, ['img']);
        emit('select-image', file);
      },
      async onDrop(evt: DragEvent) {
        const file = evt.dataTransfer?.files[0];
        if (file) {
          await validFile(file, ['img']);
          emit('select-image', file);
        }
      },
      onEnter(evt: DragEvent) {
        enterEvent.value = evt;
      },
      onLeave(evt: DragEvent) {
        if (enterEvent.value && evt.target === enterEvent.value.target) {
          enterEvent.value = undefined;
        }
      },
      clear() {
        emit('select-image', undefined);
      },
      enterEvent,
      url,
    };
  },
});
